const loginAuthMessages = (errorCode)=> {
    let message = ''
    switch(errorCode){
        case 'auth/email-already-exists':message = 'Email j√° existente' 
        break
        case 'auth/id-token-expired':message = 'Usuario expirado' 
        break
        case 'auth/insufficient-permission': message = 'Servi√ßo indisponivel no momento, sem permiss√£o'
        break
        case 'auth/internal-error': message = 'Estamos com nossos servi√ßos indisponiveis no momento, mas ja estamos trabalhando nisso'
        break
        case 'auth/invalid-password': message = 'Senha fraca, sua senha deve ter no minimo 6 caracteres'
        default: message = 'Servi√ßo indisponivel no momento'
    }

    return message
}

export default loginAuthMessages